.metaBoard {
    background: whitesmoke;
    width: 300px;
    height: 300px;
    padding-top: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 2%;
    padding: 3%;
    justify-items: space-around;
    border-radius: 2%;
    box-shadow: 5px 5px 5px 0 rgba(0, 0, 0, 0.2), inset 2px 2px 5px 0 rgba(0, 0, 0, 0.19);
    margin-left: auto;
    margin-right: auto;
    align-items: center;
}

@media only screen and (min-width: 500px) {
    .metaBoard {
        height: 500px;
        width: 500px;
        
    }
  }